.rate {
  padding-bottom: 45px;

  @include viewport--md {
    padding-bottom: 150px;
  }

  @include viewport--lg {
    position: relative;

    &::before {
      content: "";
      width: 1px;
      background-color: rgba(255, 255, 255, 0.15);
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 10%;
    }
  }
}

.rate__container {
  display: flex;
  flex-direction: column;

  @include viewport--md {
    align-items: center;
  }
}

.rate__title {
  align-self: center;
  padding-left: 28px;
  margin-bottom: 15px;
}

.rate__subtitle {
  margin-bottom: 58px;

  @include viewport--md {
    margin-bottom: 80px;
  }
}

.rate__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1400px;

  @include viewport--md {
    flex-direction: row;
    align-items: stretch;
    justify-content: center;
    position: relative;
    flex-wrap: wrap;

    &::after {
      content: "";
      width: 582px;
      height: 1px;
      background-color: $color-blue;
      bottom: -92px;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      position: absolute;
    }
  }

  @include viewport--lg {
    margin: 0 auto;
    padding: 0 20px;
    justify-content: space-around;
  }
}

.rate__item {
  margin-bottom: 25px;

  @include viewport--md {
    margin-bottom: 20px;
    margin-right: 15px;
  }

  @include viewport--lg {
    margin-bottom: 15px;
  }
}
