@font-face {
  font-family: "Aire Exterior";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/aire-exterior-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-light.woff2") format("woff2");
  font-display: swap;
}
html {
  height: 100%;
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", "Arial", sans-serif;
  color: #ababab;
  font-weight: 300;
  font-size: 21.33px;
  line-height: 32px;
  background-color: #141c24;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 767px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 768px) {
  .container {
    width: 100%;
    max-width: 1149px;
    margin: 0 auto;
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 100%;
    max-width: 1150px;
    margin: 0 auto;
    padding-left: 110px;
    padding-right: 110px;
  }
}

.link {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18.75px;
  font-weight: 500;
  color: #00c5ff;
  text-decoration: none;
  justify-self: center;
  padding: 5px 7px;
  border-bottom: 1px solid #00c5ff;
}
.link:hover {
  color: #57d6ff;
}
.link:active {
  color: #ababab;
}

.title-partition {
  color: #57d6ff;
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
  position: relative;
  margin: 0;
}
.title-partition::before {
  content: "";
  width: 50px;
  height: 1px;
  background-color: #57d6ff;
  display: inline-block;
  position: relative;
  right: 34px;
  top: -5px;
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1150px) {
  .navigation {
    flex-direction: row;
    flex-wrap: wrap;
    width: auto;
  }
}

.navigation__link {
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  color: #ffffff;
  padding: 10px 10px;
  display: block;
  text-decoration: none;
  position: relative;
}
.navigation__link:hover {
  color: #ababab;
}
.navigation__link:active {
  color: #00c5ff;
  transition: 0.3ms;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 14px 10px;
  }
}

.navigation__link--current::after {
  content: "";
  width: 300px;
  height: 1px;
  background-color: #00c5ff;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 1150px) {
  .navigation__link--current::after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #00c5ff;
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.social {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
  margin-bottom: 55px;
}
.social::after {
  content: "";
  width: 300px;
  height: 1px;
  background-color: #ffffff;
  display: block;
  position: absolute;
  bottom: -33px;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 1150px) {
  .social {
    display: none;
  }
}

.social__item:not(:last-child) {
  margin-right: 15px;
}

.subtitle-partition {
  font-family: "Aire Exterior", "Arial", sans-serif;
  font-size: 39px;
  line-height: 39px;
  font-weight: 400;
  color: #ffffff;
}

.rate-card {
  display: flex;
  flex-direction: column;
  padding-top: 76px;
  padding-left: 15px;
  padding-bottom: 50px;
  padding-right: 7px;
  background-color: #1f2631;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}
.rate-card--beneficial {
  background-color: #00c5ff;
  color: #171717;
}
@media (min-width: 768px) {
  .rate-card {
    padding-left: 47px;
    padding-right: 38px;
    padding-bottom: 44px;
  }
  .rate-card--beneficial {
    padding-left: 60px;
  }
}

.rate-card__title {
  margin: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: 42px;
  color: #ffffff;
  margin-bottom: 19px;
}
.rate-card__title--beneficial {
  color: #171717;
}

.rate-card__list,
.rate-card__list-inner {
  padding: 0;
  list-style: none;
}

.rate-card__list {
  margin-bottom: 66px;
}
@media (min-width: 768px) {
  .rate-card__list {
    flex-grow: 1;
  }
}

.rate-card__price {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 28px;
  display: block;
}

.rate-card__description {
  margin: 0;
}

.rate-card__button-choose {
  width: 282px;
  min-height: 62px;
  border: none;
  background-color: #00c5ff;
  font-family: "Aire Exterior", "Arial", sans-serif;
  color: #141c24;
  font-weight: 400;
  font-size: 24px;
  line-height: 43px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border-radius: 5px;
  margin-top: auto;
}
.rate-card__button-choose:hover {
  background-color: #141c24;
  color: #00c5ff;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .rate-card__button-choose {
    width: 321px;
    min-height: 70px;
  }
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
}
@media (min-width: 1150px) {
  .header {
    background-color: #141c24;
    position: fixed;
    width: 100%;
    z-index: 555;
    padding: 0;
    align-items: start;
    box-shadow: 0 2px 10px #00c5ff;
  }
}

.header__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 41px;
  padding-left: 38px;
  padding-right: 38px;
  padding-bottom: 30px;
}
@media (min-width: 1150px) {
  .header__container {
    flex-direction: row;
    max-width: 1440px;
    align-items: center;
    padding-top: 32px;
    padding-bottom: 35px;
    margin: 0 auto;
  }
}

.header__navigation {
  width: 100%;
  margin-bottom: 106px;
}
@media (min-width: 768px) {
  .header__navigation {
    margin: 0;
    width: auto;
  }
}

.header__logo-link {
  margin-bottom: 90px;
}
@media (min-width: 768px) {
  .header__logo-link {
    color: #00c5ff;
  }
  .header__logo-link:hover {
    color: #ababab;
  }
}
@media (min-width: 1150px) {
  .header__logo-link {
    margin: 0;
    margin-right: 99px;
  }
}

@media (min-width: 768px) {
  .header__logo-mobile {
    display: none;
  }
}

.header__logo-desktop {
  display: none;
}
@media (min-width: 768px) {
  .header__logo-desktop {
    display: block;
  }
}

.header__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.header__item:not(:last-child) {
  margin-bottom: 11px;
}
@media (min-width: 1150px) {
  .header__item:not(:last-child) {
    margin-right: 26px;
    margin-bottom: 0;
  }
}

.header__back-menu {
  background-color: transparent;
  border: none;
}
@media (min-width: 1150px) {
  .header__back-menu {
    display: none;
  }
}

.intro__card {
  background-image: url("../img/intro-background-desktop.png");
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0 0 15px 15px;
  position: relative;
  padding-top: 30px;
  padding-bottom: 80px;
  padding-left: 20px;
  padding-right: 20px;
}
.intro__card::before {
  content: "";
  width: 132px;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  bottom: 0;
  transform: rotate(90deg);
}
@media (min-width: 768px) {
  .intro__card {
    background-image: url("../img/intro-background-desktop.png");
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    padding-top: 413px;
    padding-bottom: 224px;
    max-width: 1700px;
    margin: 0 auto;
    border-radius: 30px;
    outline: 1px solid #57d6ff;
  }
}

.intro__logo-link {
  margin-bottom: 202px;
}
@media (min-width: 768px) {
  .intro__logo-link {
    display: none;
  }
}

.intro__text {
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  color: #ffffff;
  max-width: 142px;
  margin: 0;
  margin-bottom: 11px;
  align-self: start;
}
@media (min-width: 768px) {
  .intro__text {
    align-self: center;
    max-width: 220px;
    margin-bottom: 1px;
  }
}

.intro__slogan {
  font-size: 48px;
  line-height: 48px;
  font-family: "Aire Exterior", "Arial", sans-serif;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-bottom: 137px;
}
@media (min-width: 768px) {
  .intro__slogan {
    margin-bottom: 96px;
  }
}

.intro__upgrade {
  background-color: #ffffff;
  color: #171717;
  font-family: "Aire Exterior", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  border: none;
  width: 230px;
  height: 61px;
  text-transform: uppercase;
  padding: 0;
  position: relative;
  padding-left: 21px;
  margin-right: 20px;
}
.intro__upgrade::after {
  content: "";
  background-color: transparent;
  width: 21px;
  height: 61px;
  display: block;
  position: absolute;
  top: 0;
  left: 100%;
  border-left: 21px solid #ffffff;
  border-top: 24px solid transparent;
}
@media (min-width: 768px) {
  .intro__upgrade {
    margin-right: 22px;
  }
}
.intro__upgrade:hover {
  color: #00c5ff;
}

.intro__menu-toggle {
  width: 52px;
  height: 20px;
  display: flex;
  align-items: start;
  background-color: transparent;
  border: none;
  position: absolute;
  padding: 0;
  bottom: 34px;
  right: 15px;
}
@media (min-width: 1150px) {
  .intro__menu-toggle {
    display: none;
  }
}

.intro__menu-toggle span {
  width: 100%;
  height: 5px;
  background-color: #eae5e5;
  display: block;
  box-shadow: 0 15px 0 #eae5e5;
}

.manual {
  padding-top: 75px;
  padding-bottom: 110px;
  position: relative;
}
@media (min-width: 1150px) {
  .manual {
    padding-bottom: 153px;
  }
  .manual::before {
    content: "";
    width: 1px;
    background-color: rgba(255, 255, 255, 0.15);
    display: block;
    position: absolute;
    top: 5%;
    bottom: 0;
    left: 10%;
  }
}

.manual__card-description {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(5, min-content);
  justify-items: center;
  padding-bottom: 118px;
}
@media (min-width: 768px) {
  .manual__card-description {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(5, min-content);
    position: relative;
    padding-bottom: 87px;
  }
}

.manual__title-card-description {
  margin-right: -33px;
  margin-bottom: 17px;
  justify-self: center;
}
@media (min-width: 768px) {
  .manual__title-card-description {
    grid-column: 2/-1;
    grid-row: 1/2;
    justify-self: start;
    padding-left: 42px;
    margin-bottom: 23px;
  }
}

.manual__subtitle-card-description {
  justify-self: start;
  margin-bottom: 61px;
}
@media (min-width: 768px) {
  .manual__subtitle-card-description {
    grid-column: 2/-1;
    grid-row: 2/3;
  }
}

.manual__description-card {
  margin: 0;
  max-width: 310px;
  margin-bottom: 112px;
}
@media (min-width: 768px) {
  .manual__description-card {
    grid-column: 2/-1;
    max-width: 445px;
    margin-bottom: 47px;
    grid-row: 3/4;
  }
}

.manual__img-box {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .manual__img-box {
    grid-column: 1/2;
    grid-row: 2/7;
    position: absolute;
    top: -115px;
    left: -57px;
  }
}

@media (min-width: 768px) {
  .manual__img-card-description {
    width: 499px;
  }
}
@media (min-width: 1150px) {
  .manual__img-card-description {
    width: 664px;
  }
}

.manual__description-characteristic {
  margin: 0;
  max-width: 319px;
  margin-bottom: 81px;
}
@media (min-width: 768px) {
  .manual__description-characteristic {
    grid-column: 2/-1;
    max-width: 471px;
    grid-row: 4/5;
    margin-bottom: 71px;
  }
}

.manual__download-module {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18.75px;
  font-weight: 500;
  color: #00c5ff;
  text-decoration: none;
  justify-self: center;
  padding: 5px 3px;
  border-bottom: 1px solid #00c5ff;
}
@media (min-width: 768px) {
  .manual__download-module {
    grid-column: 2/-1;
    grid-template-rows: 5/6;
    justify-self: start;
  }
}

.manual__card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.manual__title {
  margin-right: -33px;
  margin-bottom: 16px;
}

.manual__subtitle {
  grid-column: 1/-1;
  grid-row: 2/3;
  margin-bottom: 90px;
  align-self: start;
}
@media (min-width: 768px) {
  .manual__subtitle {
    align-self: center;
  }
}

.manual__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: start;
  column-gap: 65px;
  row-gap: 53px;
  list-style: none;
  font-size: 21px;
  line-height: 32px;
  padding: 0;
  margin: 0;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .manual__list {
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
    column-gap: 61px;
    margin-bottom: 84px;
  }
}

.manual__item {
  padding-top: 58px;
  text-align: center;
  max-width: 130px;
}
@media (min-width: 768px) {
