    max-width: 183px;
    padding-top: 75px;
  }
}
.manual__item--account {
  background-image: url("../img/icons/icon-mail-mob.png");
  background-size: 51px 50px;
  background-position: top center;
  background-repeat: no-repeat;
  position: relative;
}
.manual__item--account::after {
  content: "";
  width: 58px;
  height: 1px;
  display: inline-block;
  background-color: #57d6ff;
  position: absolute;
  right: -58px;
  top: 24px;
}
@media (min-width: 768px) {
  .manual__item--account {
    background-image: url("../img/icons/icon-mail-desk.png");
    background-size: 64px 64px;
  }
  .manual__item--account::after {
    width: 73px;
  }
}
.manual__item--download {
  background-image: url("../img/icons/icon-download-mob.png");
  background-size: 51px 50px;
  background-position: top center;
  background-repeat: no-repeat;
  position: relative;
}
.manual__item--download::before {
  content: "";
  width: 162px;
  height: 1px;
  background-color: #57d6ff;
  position: absolute;
  transform: rotate(135deg);
  left: -120px;
  bottom: 16px;
}
@media (min-width: 768px) {
  .manual__item--download {
    background-image: url("../img/icons/icon-download-desk.png");
    background-size: 64px 64px;
  }
  .manual__item--download::before {
    display: none;
  }
  .manual__item--download::after {
    content: "";
    width: 73px;
    height: 1px;
    display: inline-block;
    background-color: #57d6ff;
    position: absolute;
    right: -58px;
    top: 24px;
  }
}
.manual__item--rate {
  background-image: url("../img/icons/icon-joystick-mob.png");
  background-size: 56px 56px;
  background-position: top center;
  background-repeat: no-repeat;
  padding-top: 67px;
}
@media (min-width: 768px) {
  .manual__item--rate {
    background-image: url("../img/icons/icon-joystick-desk.png");
    background-size: 64px 64px;
    padding-top: 75px;
  }
}
.manual__item--game {
  background-image: url("../img/icons/icon-arcade-mob.png");
  background-size: 57px 57px;
  background-position: top center;
  background-repeat: no-repeat;
  padding-top: 66px;
  position: relative;
}
.manual__item--game::before {
  content: "";
  width: 65px;
  height: 1px;
  display: inline-block;
  background-color: #57d6ff;
  position: absolute;
  left: -59px;
  top: 27px;
}
@media (min-width: 768px) {
  .manual__item--game {
    background-image: url("../img/icons/icon-arcade-desk.png");
    background-size: 57px 57px;
    padding-top: 75px;
  }
  .manual__item--game::before {
    width: 65px;
  }
}

.rate {
  padding-bottom: 45px;
}
