.advantages {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../img/advantages-background-mobile.png");
  background-position: top 153px center;
  background-size: 525px 357px;
  background-repeat: no-repeat;
  padding-bottom: 25px;
}
@media (min-width: 768px) {
  .advantages {
    background-image: url("../img/advantages-background-tablet.png");
    background-repeat: no-repeat;
    background-size: 597px 544px;
    background-position: top 0 right -313px;
    padding-top: 186px;
  }
}
@media (min-width: 1150px) {
  .advantages {
    background-image: url("../img/advantages-background-desktop.png");
    background-repeat: no-repeat;
    background-size: 900px 802px;
    background-position: top right -409px;
    padding-top: 186px;
  }
}

.advantages__container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
